@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import '../../styles/common';
@import '../../styles/variables';

.TileGrid {
    display: block;
    margin: 0;
    padding: 0;

    &__row {
        gap: $spacing-05;
        display: flex;
        flex-direction: row;
    }

    &__cell {
        min-width: $tile-width;
        max-width: 360px;
        flex: 1 0 $tile-width;
    }

    .Link.focus {
        .cp_basic-tile__picture-wrapper {
            border: 3px solid $color-itvx-brand-primary;
        }
    }

    .FocusFrame {
        display: none;
    }
}
