@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.CollectionContent {
    margin-top: $spacing-09;

    &__title {
        position: relative;
        @include itv-font-type(subtitle-2, bold);
        text-transform: uppercase;
        white-space: nowrap;
        margin-bottom: $spacing-04;
    }

    &__tileGrid {
        position: relative;
        height: auto;
    }

    &__gradient {
        z-index: 4;
        position: absolute;
        bottom: 0;
        height: 131px;
        left: -50px;
        right: -$app-padding-right;
    }
}
